import { Component, OnInit } from '@angular/core';
import { first } from 'rxjs/operators';
import { FormBuilder, FormGroup } from '@angular/forms';


import { Router } from '@angular/router';
import { BatchImportService } from '../../services/batchimportservice';
import { AlertService } from '../../../services';

@Component({
  selector: 'app-admin-batch-import',
  templateUrl: './batch-import.component.html',
  styleUrls: ['./batch-import.component.css']

})
export class BatchImportComponent implements OnInit {
  constructor(private formBuilder:FormBuilder, private batchImportService: BatchImportService, 
  private alertService :AlertService){}

  userForm:FormGroup;
  assetForm:FormGroup;
  questionForm:FormGroup;
  public userBatchFile : string = "No file choosen";
  public assetBatchFile : string = "No file choosen";
  public questionBatchFile : string = "No file choosen";
  public userFile;

  
  ngOnInit(): void {
    this.assetForm = this.formBuilder.group({
      batchImportAssetFile: ['']
    });
    this.userForm = this.formBuilder.group({
      batchImportUserFile : ['']
    })  
    this.questionForm = this.formBuilder.group({
      batchImportQuestionFile : ['']
    })    
     
  }
  onSubmitBatchImportQuestionForm(event){
    if(this.questionForm.controls.batchImportQuestionFile.value.length == 0){
      this.alertService.success("Please select file for import questions.");
    }else{
      var questionFormData = new FormData();
      questionFormData.append('file', this.questionForm.controls.batchImportQuestionFile.value);
      questionFormData.append('inputFileName', this.questionForm.controls.batchImportQuestionFile.value.name);
      this.batchImportService.batchImportQuestion(questionFormData)
      .pipe().subscribe(
        data =>{
          var successCount = 0;
          var failCount = 0;
          if(data.length == 0){
            this.alertService.success("Total question stored: 0");
          }else if(data.length >= 1){
            for(var R = 0; R < data.length; R++){
              var currentElement = data[R];
              console.dir(currentElement);
              if( currentElement.processMessage == "Stored.."){
                successCount++;
              }else{
                failCount++;
              }
            }
            this.alertService.success("Total questions stored: "+ successCount +" , Fail : "+ failCount);
          }
  
        },
        erroor => {
          this.alertService.error("Error !");
        }
      );

    }
  }

  onQuestionFileChange(event) {
    if (event.target.files.length > 0) {
      var questionFile = event.target.files[0];
      this.questionBatchFile = questionFile.name;
      setTimeout(()=>{    
        this.questionForm.get('batchImportQuestionFile').setValue(questionFile);
      }, 200); 
    }
  }

  onSubmitBatchImportAssetForm(event){
     
    if(this.assetForm.controls.batchImportAssetFile.value.length == 0){
      this.alertService.success("Please select file for import assets.");
    }else{
      var formData2 = new FormData();
      formData2.append('file', this.assetForm.controls.batchImportAssetFile.value);
      formData2.append('inputFileName', this.assetForm.controls.batchImportAssetFile.value.name);
      this.batchImportService.batchImportAsset(formData2)
      .pipe().subscribe(
        data =>{
          
          var successCount = 0;
          var failCount = 0;
          if(data.length == 0){
            this.alertService.success("Total Asset stored: 0");
          }else if(data.length >= 1){
            for(var R = 0; R < data.length; R++){
              var currentElement = data[R];
              console.dir(currentElement);
              if( currentElement.processMessage == "Asset Stored.."){
                successCount++;
              }else{
                failCount++;
              }
            }
            this.alertService.success("Total asset stored: "+ successCount +" , Fail : "+ failCount);
          }
        },
        erroor => {
          this.alertService.error("Error !");
        }
      );

    }
  }
  onAssetFileChange(event) {
    if (event.target.files.length > 0) {
      var assetFile = event.target.files[0];
      this.assetBatchFile = assetFile.name;
      setTimeout(()=>{    
        this.assetForm.get('batchImportAssetFile').setValue(assetFile);
      }, 200); 
    }
  }
  ////--------------------------------------------------------------------------------------------------------
  onSubmitBatchImportUserForm(event){
    if(this.userForm.controls.batchImportUserFile.value.length == 0){
      this.alertService.success("Please select file for import users.");
    }else{
      var formData = new FormData();
      formData.append('file', this.userForm.controls.batchImportUserFile.value);
      formData.append('inputFileName', this.userForm.controls.batchImportUserFile.value.name);
      this.batchImportService.batchImportUser(formData)
      .pipe().subscribe(
        data =>{
          var successCount = 0;
          var failCount = 0;
          if(data.length == 0){
            this.alertService.success("Total user stored: 0");
          }else if(data.length >= 1){
            for(var R = 0; R < data.length; R++){
              var currentElement = data[R];
              console.dir(currentElement);
              if( currentElement.processMessage == "Stored..!"){
                successCount++;
              }else{
                failCount++;
              }
            }
            this.alertService.success("Total user stored: "+ successCount +" , Fail : "+ failCount);
          }
        },
        erroor => {
          this.alertService.error("Error !");
        }
      );

    }
  }
  onUserFileChange(event) {
    if (event.target.files.length > 0) {
      this.userFile = event.target.files[0];
      this.userBatchFile = this.userFile.name;
      setTimeout(()=>{    
        this.userForm.get('batchImportUserFile').setValue(this.userFile);
      }, 200);  
    }
  }
///----------------------------------------------------------
openTemplate(templateName:string):any{
  this.batchImportService.openTemplate(templateName)
  .pipe()
     .subscribe(content => {
      if(navigator.appVersion.toString().indexOf('.NET') > 0){
        var blob = new Blob([content], { type: content.type});
        window.navigator.msSaveBlob(blob);
      }else{
        var blob = new Blob([content], { type: content.type}),
        url = window.URL.createObjectURL(blob);
        window.open(url);
      }
     });
 }
 
}